.dashboard__reports {
  display: flex;
  flex-direction: column;
}

.dashboard__reports-buttons {
  display: flex;
  justify-content: center;
  margin-top: 70px;
}

.dashboard__reports-buttons button {
  padding: 10px 20px;
  font-family: "Montserrat";
  font-size: small;
  margin: 0 10px;
}

#content > table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 50%;
  max-width: 550px;
}

#content th {
  font-size: large;
  font-weight: 800;
  letter-spacing: 0.5px;
  text-transform: capitalize;
}

#content td {
  font-size: medium;
  font-weight: 600;
}

#content td,
#content th {
  border: 2px solid #ccc;
  text-align: left;
  padding: 8px;
}
tr:nth-child(even) {
  background-color: #dddddd;
}
